<template class="footerHtml">
  <ul class="flex footerIconBox" v-bind:class="{dispalyNone: dispaly}">
	<li>
		<router-link to="/information">
			<img src="../assets/wenzhangfabu@3x.png">
			<p>首页</p>
		</router-link>
	</li>
	<li>
		<router-link to="">
			<img src="../assets/kechengguanli@3x.png">
			<p>消息</p>
		</router-link>
	</li>
	<li>
		<router-link to="/post/add">
			<img src="../assets/kechengguanli@3x.png">
			<p>发布</p>
		</router-link>
	</li>
	<li>
		<router-link to="">
			<img src="../assets/quanziguanli@3x.png">
			<p>圈子</p>
		</router-link>
	</li>
	<li>
		<router-link to="/user/user" router-link-active="active">
			<img src="../assets/jingbiaohuida@3x.png">
			<p>我的</p>
		</router-link>
	</li>
 </ul>
</template>

<script>
	export default {
	  name: 'footerHtml',
	  data () {
	    return {
	      dispaly: false
	    }
	  },
	  created:function(){
	  	if(!this.utilIsLocalhost){
			this.dispaly = true
	  	}
	  }
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.footerIconBox{position: fixed;bottom: 0;left: 0;right: 0;text-align:center;box-shadow: -2px 1px 1px #666666;background:#fff;}
	.footerIconBox li{padding: 1rem;}
	.footerIconBox li a{color: #666;}
	.footerIconBox li.active{color:#FF1919;}
	.footerIconBox img{width: 1.5rem;height: 1.5rem;}
	.dispalyNone{display: none;}
</style>



